/* You can add global styles to this file, and also import other style files */
@import '~@angular/material/prebuilt-themes/indigo-pink.css';
@import '~bootstrap-4-grid/scss/grid';

@import '~@angular/material/theming';

@include mat-core();

$primary: mat-palette($mat-indigo, 700);
$accent: mat-palette($mat-deep-orange, 500, A100, A400);
$warn: mat-palette($mat-red, 600);

$theme: mat-light-theme($primary, $accent, $warn);

@include angular-material-theme($theme);

body,
html {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  height: 100%;
  background-color: $dark;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}

.h100 {
  height: 100%;
}

.list-item-active {
  font-weight: bold;
  color: mat-color($primary, darker) !important;
  background: rgba(0, 0, 0, 0.04);
}

.border-left {
  border-left: 4px solid mat-color($primary, darker) !important;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -50px; //Half of the height of the loading graphic
  margin-left: -50px; //Half of the width of the loading graphic
  &:before {
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

table {
  width: 100%;
}

.mat-spinner {
  z-index: 1000;
}

/***  Fonts ***/
.f {
  &-50 {
    font-size: 50px;
  }
  &-small {
    font-size: 1rem;
  }
  &-medium {
    font-size: 1.5rem;
  }
  &-large {
    font-size: 2rem;
  }
}

.float {
  &-right {
    float: right;
  }
  &-left {
    float: left;
  }
}

.notify-success {
  color: white !important; // green
  background-color: green !important;
  border-color: green !important;
  font-size: 20px;
  .mat-simple-snackbar-action {
    color: white !important;
  }
}
.mat-drawer-content {
  overflow: hidden !important;
}

/**** Drag ****/
/* in-flight clone */
.gu-mirror {
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 0.8;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=80)';
  filter: alpha(opacity=80);
  pointer-events: none;
}
/* high-performance display:none; helper */
.gu-hide {
  left: -9999px !important;
}
/* added to mirrorContainer (default = body) while dragging */
.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}
/* added to the source element while its mirror is dragged */
.gu-transit {
  opacity: 0.2;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=20)';
  filter: alpha(opacity=20);
}

table {
  width: 100%;
  .mat-table-sticky {
    z-index: 0 !important;
  }
}
.table-container {
  overflow: auto;
  max-height: 500px;
}
